<template>
    <div class="bs-catalog-administration-title-copy">
        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane
                v-for="item in typeList"
                :key="item.type_id"
                :label="item.type_name" 
                :name="item.type_name"
            ></el-tab-pane>
        </el-tabs>
        <catalogTable :typeList='typeList' :num='num' @modify='modify'></catalogTable>
        <div class="catalog-administration-title-copy-change-data">
            <catalogChangeType :typeList='typeList' @modifyTypeSuccess='modifyTypeSuccess'></catalogChangeType>
            <catalogAddType :typeList='typeList'></catalogAddType>
        </div>
        <modal v-show="ismodal" :modalData='modalData' @toggleModle='toggleModle' @close='closeModal' ></modal>
    </div>
</template>

<script>
import * as ajax from "@/api";
import catalogTable from './catalogTable'
import catalogChangeType from './catalogChangeType'
import catalogAddType from './catalogAddType'


export default {
  data() {
    return {
      options: [],
      value: "",
      typevalue:'',
      input: "",
      activeName:'',
      typeList :'',
      num: 0, //选中的标签
      ismodal:false,    //是否显示模态框
      modalData:''  //给模块传递的数据
    };
  },
  created() {
      ajax.getOutlineList().then(resp=>{
          this.typeList = resp.data.data.article_category  //请求文章类型列表
      })
  },
  components:{
    catalogTable,
    catalogChangeType,
    catalogAddType,
    modal:resolve => require(['./modal'], resolve)
  },
    
  methods: {
      handleClick(tab) {
        this.num = Number(tab.index) //选择文章类型
    },
    modify(data){
        this.ismodal = true         //显示模态框
        this.modalData = data      //接收选择信息的数据
    },
    toggleModle(data){
        this.typeList = data    //更新数据
        this.ismodal = false  //隐藏模态框
    },
    closeModal(){
        this.ismodal = false  //隐藏模态框
    },
    modifyTypeSuccess(data){
        this.typeList = data //修改文章类型成功
    }
  },
  
};
</script>

<style lang='scss'>
.bs-catalog-administration-title-copy{
    flex: 1;
        .el-tabs{
            padding-left: 25px;
            .el-tabs__content{
            .el-tab-pane{
                display: none !important;
            }
        }
        .el-tabs__nav-wrap::after{
            background-color: rgb(213, 216, 221)
        }
    }
    
    .catalog-administration-title-copy-change-data{
        display: flex;
    }
    .bs-catalog1-table{
        padding-left: 25px;
    }
}
</style>