<template>
  <div class="bs-catalog-add-title">
    <div class="catalog-add-title-header">添加更多</div>
    <div class="catalog-add-title-main">
      <el-select v-model="value" placeholder="请选择文章类型">
        <el-option
          v-for="item in options"
          :key="item.type_id"
          :label="item.type_name"
          :value="item.id"
        ></el-option>
      </el-select>
      <el-input 
        v-model="typevalue" 
        placeholder="输入添加文章类型" 
        class="el-input-article-type"
        v-show="value==0 ? true : false"
      ></el-input>

      <el-input v-model="input" placeholder="请输入文章大纲" v-show="value==0 ? false : true" class="el-input-article-title"></el-input>
      <el-button type="primary" plain @click="handleSubType">添加</el-button>
    </div>
  </div>
</template>

<script>
import * as ajax from "@/api";

export default {
  data() {
    return {
      options: [],
      value: "",
      typevalue:'',
      input: ""
    };
  },
  created() {},
  props: ["typeList"],
  watch: {
    typeList(val) {
      this.options = [
        { id: 0, type_describe: "空", type_id: "0", type_name: "新建文章类型" },
        ...val
      ];
    },
    
  },
  methods: {
    handleSubType() {
      if (this.value === '') {        //不选择文章类型
        alert("请求选择文章类型");
      } else {
        if(this.value === 0){         //当选择新建文章类型
          if(this.input === ''){      //添加文章类型
            if(confirm('确定添加文章类型吗？')){
                ajax.addArticleType(this.typevalue).then(resp=>{
                    this.$emit('modifysuccess',resp.data.data.article_category)
                })
            }
          }}
          else{
          var val = ''
          this.options.forEach((item,index)=>{
            if(item.id === this.value){
              val=item.type_name
            }
          })
          if(confirm(`是否在${val}下添加${this.input}？`)){
              ajax.addSecondTitle(this.input,this.value,'').then(resp=>{
                  if(resp.data.code){
                    this.$notify({
                      title: '成功',
                      message: '添加成功',
                      type: 'success'
                    });
                  }
                  this.$emit('modifysuccess',resp.data.data.article_category)
              })

          }
        }

      }
    }



  }
};
</script>

<style lang='scss'>
.bs-catalog-add-title {
    width: 50%;
    padding: 20px 0 0 30px;
    border-top: 2px solid rgb(181, 197, 38);
  .catalog-add-title-header {
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    line-height: 50px;
    margin-bottom: 30px;
  }
  .catalog-add-title-main {
    .el-input-article-title{
      width: 40%;
      display: block;
      margin-top: 30px;
    }
    .el-input-article-type{
        width: 40%;
        margin-left: 50px;
    }
  }
  .el-button--primary {
    margin-top: 20px;
    display: block;
  }
}
</style>