<template>
  <div class="bs-catalog1-table">
    <el-table :data="bookList['all_book']" style="width: 100%" max-height="250">
      <el-table-column fixed prop="book_name" label="大纲标题" width="300"></el-table-column>
      <el-table-column prop="book_describe" label="文章描述" width="500"></el-table-column>
      <el-table-column fixed="right" label="操作" width="500">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="modifyOutline(scope.$index,bookList['all_book'])"
            type="text"
            size="small"
          >修改标题</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  methods: {
    modifyOutline(index, list) {
      this.$emit('modify',list[index])
    }
  },
  data() {
    return {
      bookList: []
    };
  },
  props: ["typeList",'num'],
  created() {
    
  },
  watch: {
    typeList(val) {
      this.bookList = val[0];
    },
    num(val){
        val = Number(val)
        this.bookList = this.typeList[val]
    }
  }
};
</script>

<style lang='scss'>
.bs-catalog1-table {
  .el-table {
    .cell {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .el-table__body-wrapper {
      overflow-y: hidden;
    }
    max-height: 100% !important;
    .el-table__body-wrapper {
      max-height: 100% !important;
    }
  }
  .el-table__fixed-body-wrapper {
    max-height: 100% !important;
  }
}
</style>