<template>
  <div class="catalog-add-title-copy-change-data-type">
    <p class="catalog-add-title-copy-change-data-type-title">修改文章类型</p>
    <el-select v-model="value_id" placeholder="请选择">
      <el-option 
        v-for="item in options" 
        :key="item.value" 
        :label="item.label" 
        :value="item.value"
    ></el-option>
    </el-select>
    <el-input v-model="value" placeholder="请输入文章类型"></el-input>
    <el-input v-model="describe" placeholder="请输入文章描述"></el-input>
    <el-button type="primary" round @click="modifyType">修改</el-button>
  </div>
</template>


<script>
import * as ajax from "@/api";

export default {
    props: ["typeList"],
    data() {
        return {
            options: [],
            value: '',
            value_id:'',
            describe:''
        }
    },
    watch:{
        typeList(val){
            val.forEach(item => {
                item['value'] = item['id']
                item['label'] = item['type_name']
            });
            this.options = val
        },
        value_id(val){
            this.value = this.options[val-1]['type_name']
        }
    },
    methods:{
        modifyType(){
            if(!this.value_id){
                alert('请选择文章类型')
            }
            else{ajax.modifyArticleType(this.value_id,this.value,this.describe).then(resp=>{
                if(resp.data.code){
                    alert('修改成功')
                    this.$emit('modifyTypeSuccess',resp.data.data.article_category)
                }
            })}
        }
    }
};
</script>


<style lang='scss'>
.catalog-add-title-copy-change-data-type {
    width: 50%;
    padding: 20px 0 0 30px;
    border-top: 2px solid rgb(181, 197, 38);
    &-title{
        height: 50px;
        line-height: 50px;
        font-size: 30px;
        font-weight: bold;
    }
    .el-input{
        width: 300px;
        display: block;
        margin-top: 30px;
    }
    .el-button--primary{
        margin-top: 20px;
    }
}
</style>